@import "./header.less";
@import "./section.less";
@import "./params/var.less";
@import "./params/mix.less";
@import "./footer.less";

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: Lato;
  .click_top {
    position: fixed;
    right: 90px;
    display: none;
    top: 300px;
    z-index: 111;
    .sm-block({
        right: 10px;
        top: 100px;
    });
    .scroll_top {
      color: #ff4848;
      background-color: transparent;
      font-size: 40px;
      border-radius: 5px;
      border: none;
      text-shadow: 2px 2px 2px black;
      .sm(font-size, 30px);
    }
  }
  .btn{
    padding: 18px 56px;
    background-color: #3dd2ae;
    border: none;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 14px;
    text-align: center;
    border-radius: 30px;
    color: white;
    transition: 500ms;
    &:hover{
      box-shadow: 2px 2px 10px black;
    }
  }
  .box_img{
    width: 100%;
    height: auto;
  }
}
