footer {
  padding-top: 80px;
  padding-bottom: 145px;
  background-color: #1a2124;
  .md-block({
    padding-top: 50px;
    padding-bottom: 100px;
  });
  .sm-block({
    padding-top: 20px;
    padding-bottom: 50px;
  });
  h3 {
    color: white;
    font-weight: 600;
    font-size: 13px;
    text-transform: capitalize;
    margin-bottom: 30px;
    margin-top: 20px;
  }
  p {
    color: white;
    font-size: 13px;
    line-height: 30px;
  }
  .text_box {
    margin-top: 70px;
    display: flex;
    justify-content: space-between;
    .sm-block({
        display: block;
        text-align: center;
    });
    ul{
        .sm-block({
            margin: 15px 0px;
        });
    }
    .text_title {
      text-transform: capitalize;
      font-size: 13px;
      font-weight: 600;
      margin-bottom: 30px;
      line-height: 0px;
     
    }
    li{
        margin: 10px 0px;
        a{
            text-decoration: none;
            text-transform: capitalize;
            color: white;
            font-size: 13px;
            font-weight: 300;
        }
    }
  }
  form{
    position: relative;
   
      input{
          width: 70%;
          border: none;
          padding: 20px 0px;
      }
      button{
          position: absolute;
          border: none;
          color: white;
          background-color: #835fa8;
          top: 0;
          right: 30%;
          padding: 17px 25px;
          i{
            font-size: 20px;
          }
      }
     
  }
 
}
