header {
  background: url(../img/Tork_01.jpg) no-repeat center center / cover;
  width: 100%;
  height: auto;
  .header_top {
    padding-top: 45px;
    nav {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      .right {
        display: flex;
        .header_search{
          .sm(text-align, center);
        }
        input {
          padding: 18px 100px 18px 30px;
          border: none;
          border-radius: 30px;
          background-color: #343c48;
          color: #9da5b0;
          font-size: 14px;
          text-transform: capitalize;
          margin-right: 14px;
          .sm-block({
            padding: 10px 50px 10px 20px;
            font-size: 12px;
            margin-bottom: 5px;
          });
        }
        .btn_buy {
          background-color: @ab;
          padding: 18px 35px;
          color: white;
          text-transform: capitalize;
          font-size: 15px;
          margin-right: 22px;
          border: none;
          border-radius: 30px;
          .sm-block({
            padding: 10px 50px 10px 20px;
            font-size: 12px;
          });
        }
        .bars {
          margin-top: auto;
          margin-bottom: auto;
          .bar {
            border: none;
            background-color: transparent;
            align-self: center;
            i {
              font-size: 20px;
              color: white;
              display: block;
            }
          }
        }
      }
      .nav_texts {
        display: none;
        ul {
          display: flex;
          li {
            margin-right: 10px;
            transition: 500ms;
            &:hover {
              &::after {
                content: "";
                display: block;
                width: 50%;
                height: 2px;
                background-color: @ab;
                margin: auto;
                margin-top: 5px;
              }
            }
            a {
              text-decoration: none;
              text-transform: uppercase;
              color: blue;
              font-size: 14px;
              font-weight: 500;
            }
          }
        }
      }
    }
  }

  .header_title {
    padding-bottom: 250px;
    margin-top: 250px;
    margin-left: 30%;
    .lg-block({
      padding-bottom: 200px;
      margin-top: 200px;
    });
    .md-block({
      padding-bottom: 100px;
      margin-top: 100px;
    });
    .sm-block({
      padding-bottom: 50px;
      margin-top: 50px;
      margin-left: 20%;
    });
    h1 {
      font-size: 70px;
      color: white;
      text-transform: capitalize;
      margin-bottom: 40px;
      .lg-block({
        font-size: 50px;
        margin-bottom: 30px;
      });
      .md-block({
        font-size: 30px;
        margin-bottom: 25px;
      });
      .sm-block({
        font-size: 30px;
        margin-bottom: 10px;
      });
      span {
        display: block;
      }
    }
    p{
      color: white;
      font-size: 18px;
      text-transform: capitalize;
      line-height: 30px;
      .md-block({
        font-size: 15px;
      });
      .sm-block({
        font-size: 12px;
      });
    }
  }
}
