section {
  .section_first {
    background-color: @ab;
    width: 100%;
    height: auto;
    position: relative;
    padding-bottom: 165px;
    .md(padding-bottom, 100px);
    .sm(padding-bottom, 110px);
    h2 {
      color: white;
      text-transform: capitalize;
      font-size: 40px;
      margin-bottom: 30px;
      margin-top: 100px;
      .md-block({
        font-size: 30px;
        margin-top: 80px;
      });
      .sm-block({
        font-size: 20px;
        margin-top: 80px;
      });
    }
    p {
      color: white;
      line-height: 30px;
      font-size: 16px;
      margin-bottom: 40px;
      .sm-block({
        font-size: 14px;
      });
    }
    .img_box {
      position: absolute;
      top: -100px;
      .md(top, -80px);
      .sm(top,  60px);
    }
  }
  .section_second {
    padding-top: 90px;
    margin-bottom: 100px;
    .text_item {
      .lg-block({
        margin: 40px 0px;
      });
      .md-block({
        margin: 40px 0px;
      });
      .sm-block({
        margin: 40px 0px;
      });
      h3 {
        text-transform: capitalize;
        color: #2b303b;
        font-size: 30px;
        font-weight: 300;
        margin-bottom: 40px;
      }
      p:first-of-type {
        font-weight: 300;
        font-size: 20px;
        color: #696f7c;
        line-height: 30px;
        margin-bottom: 40px;
        text-transform: capitalize;
      }
      p:last-of-type {
        color: #44484f;
        font-size: 14px;
        font-weight: 400;
        line-height: 30px;
        margin-bottom: 40px;
        text-transform: capitalize;
      }
    }
  }
  .section_three {
    background: url(../img/bg22_02.jpg) no-repeat top center;
    padding-top: 100px;
    h2 {
      text-transform: capitalize;
      color: #ffffff;
      font-size: 40px;
      font-weight: 300;
      margin-bottom: 40px;
      text-align: center;
    }
    p {
      color: #9da5b0;
      font-size: 16px;
      line-height: 30px;
      text-align: center;
    }
    p:last-of-type {
      margin-bottom: 100px;
    }
    .img_box {
      text-align: center;
    }
  }
  .section_three_bottom {
    background-color: #2b303b;
    padding-top: 80px;
    padding-bottom: 150px;
    .text_box {
      .text_icons {
        margin-bottom: 30px;
        i {
          color: #3dd1ad;
          font-size: 40px;
          font-weight: 300;
        }
      }
      h3 {
        color: white;
        font-size: 20px;
        font-weight: 300;
        text-transform: uppercase;
        margin-bottom: 30px;
        span {
          color: #3a4d60;
        }
      }
      p {
        text-align: left;
        text-transform: capitalize;
        font-size: 14px;
        color: #9da5b0;
        margin-bottom: 0;
      }
    }
  }
  .section_four {
    .section_four_bottom {
      background: url(../img/Tork_0s1_02.jpg) no-repeat center bottom;
      padding-bottom: 200px;
      .md(padding-bottom, 100px);
      .sm(padding-bottom, 50px);
    }
    .box_img {
      height: 100%;
      img {
        height: 100%;
      }
    }
    h3 {
      padding-top: 100px;
      font-weight: 300;
      font-size: 40px;
      color: black;
      text-transform: capitalize;
      margin-bottom: 35px;
      .md(padding-bottom, 80px);
      .md(font-size, 30px);
      .sm(padding-bottom, 50px);
      .sm(font-size, 20px);
      i {
        font-size: 30px;
        color: #9578b5;
        margin-right: 25px;
        font-weight: 100;
      }
    }
    .text_light {
      font-weight: 300;
      font-size: 20px;
      color: #696f7c;
      line-height: 30px;
      margin-bottom: 40px;
      .sm(font-size, 14px)
    }
    p {
      font-size: 14px;
      color: #44484f;
      font-weight: 400;
      line-height: 30px;
      margin-bottom: 100px;
      .md(margin-bottom, 80px);
      .sm(margin-bottom, 50px);
    }
    .box {
      padding-bottom: 70px;
      background-color: #835fa8;
      padding-left: 60px;
      padding-top: 70px;
      .sm-block({
      padding-left: 10px;
      padding-top: 30px;
      });
      .text_four_box {
        color: white;
        text-transform: capitalize;
        font-size: 32px;
        font-style: italic;
        line-height: 30px;
        margin-bottom: 0;
        .sm-block({
         font-size: 20px;
          });
        span {
          display: block;
          font-size: 12px;
          font-style: italic;
        }
      }
      .text_boxs {
        display: flex;
        margin-top: 35px;
        margin-bottom: 30px;
        p {
          font-size: 15px;
          color: white;
          margin-left: 25px;
          text-transform: capitalize;
        }
      }
    }
    .item_text {
      padding-left: 40px;
      padding-right: 70px;
      h3 {
        font-size: 23px;
        font-weight: 300;
        padding-top: 40px;
        margin-bottom: 20px;
        line-height: 30px;
        color: black;
      }
      p {
        color: #83969f;
        line-height: 30px;
        font-size: 14px;
        font-weight: 300;
      }
    }
  }
}
